.story{
  position:relative;
  background-position:center center;
  background-size:cover;
  background-repeat:no-repeat;
  width:120px;
  height:200px;
  border-radius: 10px;
  box-shadow: 0px 5px 17px -7px rgba(0,0,0,0.75);
  margin-right: 10px;
  cursor: pointer;
  transition:transform 100ms ease-in;
}

.story:hover{
  transform:scale(1.07);
}

.story__avatar{
  margin:10px;
  border: 5px solid #2927CF;
}

.story > h4{
  position:absolute;
  bottom:20px;
  left:20px;
  color: white;
}